@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.font-big {
  font-weight: 600;
  font-size: 30px;
  line-height: 1.4;
}

@media (min-width: 768px) {
  .font-big {
    font-size: 64px;
  }
}

@media (min-width: 1200px) {
  .font-big {
    font-size: 104px;
  }
}

.font-mid {
  font-family: 'Articulat CF', sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 1;
}


.transition-height {
  transition-property: height;
}


.mobile-menu {
  height: 0;
  display: none;
}

.mobile-menu-close {
  animation: slideOut 0.2s ease-out 1 forwards;
}

.mobile-menu-open {
  animation: slideIn 0.2s ease-in 1 forwards;
}



@keyframes slideIn {
  from {
    height: 0;
    display: none;
  }
  to {
    height: calc(100vh - 49px);
    display: block;
  }
}

@keyframes slideOut {
  from {
    height: calc(100vh - 49px);
    display: block;
  }
  to {
    height: 0;
    display: none;
  }
}

/* .landing {
  height: calc(100vh - 49px);
} */

.cursor {
  height: 0.91em;
  width: 2px;
  background-color: black;
  margin-left: 5px;
  margin-right: 5px;
  transform: translateY(2px);
}

.cursor.blink {
  animation: blink 1.2s linear infinite;
}

@keyframes blink {
  0% {opacity: 0;}
  1% {opacity: 1;}
  49% {opacity: 1;}
  50% {opacity: 0;}
  100% {opacity: 0;}
}